@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,700);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #4c0b12;
  min-height: 100vh;
  display: flex;
  flex-direction: row; 
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  background-image: url(/static/media/capa.8f260fef.jpg);
  -webkit-filter: invert(15%);
          filter: invert(15%);
  background-position: center;
  background-size: cover;
}

.globo {
  /* display: none; */
  order: 0;
  font-size: 1.7em;
  position: absolute;
}

#languagePick {
  order: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  height: 190px;
  -webkit-transition-duration: 0.5s;
          transition-duration: 0.5s;
  position: fixed;
  align-self: flex-start;
  margin-top: 5vmin;
}

#languagePick div {
  -webkit-transition: 0.5s;
  transition: 0.5s;
  margin: 10px;
}

#languagePick > div:hover {
  cursor: pointer;
  color: slategray;
}


.fa-globe-americas {
  align-self: flex-start;
  position: fixed;
  margin: 5vmin 7vmin 0 0;
}

.fa-globe-americas:hover + #languagePick {
  opacity: 1;
}

#languagePick:hover {
  opacity: 1;
}

.bandeiras {
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  margin: 0 2vmin 0 2vmin;
}

.bandeiraEspanha, .bandeiraUSA, .bandeiraItalia {
  width: 58px;
}

.bandeiraEspanha:hover, .bandeiraUSA:hover, .bandeiraItalia:hover {
  width: 64px;
}

.bandeiraBrasil{
  width: 54px;
} 

.bandeiraBrasil:hover {
  width: 58px;
}

#bioSection {
    display: flex;
    flex-direction: column;
}

#bioText {
    order: 0;
    text-align: left;
    text-align: initial;
    font-size: 11vmin;
    padding: 0 12vmin;
}

#autor {
    order: 1;
    font-size: 4vmin;
    padding: 0 12vmin;
    text-align: right;
}
.albunsCapas {
    background-color: #bba34d;
    display: flex;
    align-items: center;
    justify-content: center;
}

.fp-section {
    text-align: center
}

.capas {
    width: 60vmin;
    height: 60vmin;
}

.fab {
    font-size: calc(15px + 6vmin);
    margin: 5vmin 5vmin 0 5vmin
}

.fab.fa-spotify:hover {
    color: #1e7d15
}

.fab.fa-youtube:hover {
    color: #dc1313
}

.btn-social:hover {
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both;
    -webkit-animation-name: fadeIn;
            animation-name: fadeIn;
    cursor: pointer;
    -webkit-transition: all 1s;
    transition: all 1s;
  }
  
  @-webkit-keyframes fadeIn {
    0% {opacity: 0.2;}
    100% {opacity: 1;}
  }
  
  @keyframes fadeIn {
    0% {opacity: 0.2;}
    100% {opacity: 1;}
  }
#container {
    margin: 0;
    padding: 0;
    position: relative;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: row;
    align-items: center;
}

#map {
    height: 100%;
    width: 100%;
    float: right;
    position: absolute !important;
	-webkit-overflow-scrolling: auto;
}

#parteSemMapa {
    display: flex;
    height: 100%;
    width: 50vmin;
    float: left;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    z-index: 1;
    background-color: rgba(255, 255, 255, 0.6);
}

#title {
    height: 5vmin;
}

#addMarcadorNoBancoBtn {
    display: flex;
    height: 6vmin;
    width: 90%;
    margin-bottom: 6vmin;
    justify-self: flex-end
}

#toggleVisaoMapa {
    align-self: flex-start;
    z-index: 2;
    position: absolute;
    width: 43px;
    display: flex;
    height: 38px;
    align-items: center;
    justify-content: center;
    border-radius: 4px;
    right: 0;
    margin: 7px;
    background-color: #fff;
}

#iconVisaoNoturna {
    color: #242f3e;
}

#iconVisaoRetro {
    color: #dbb449;
}

#titleMobile, 
#addMarcadorNoBancoBtnMobile, 
#divAddMarcadorNoBancoBtnMobile {
    display: none;
}

@media (max-width: 584px), (max-height: 584px) {
    #container {
        height: 100%;
    }
    
    #map {
        height: 100%;
        position: absolute !important;
        width: 100%;
        -webkit-overflow-scrolling: auto;
    }
    
    #titleMobile {
        width: 100%;
        height: 55px;
        z-index: 1;
        display: flex;
        justify-content: center;
        align-items: center;
        position: fixed;
        background-color: #3f51b5;
        color: #fff;
        font-family: "Roboto", "Helvetica", "Arial", sans-serif;
        font-weight: 500;
        font-size: 17px;
        text-transform: uppercase;    
        align-self: flex-start;
    }

    #divAddMarcadorNoBancoBtnMobile {
        display: flex;
        width: 100%;
        justify-content: center;
    }

    #addMarcadorNoBancoBtnMobile {
        display: flex;
        width: 170px;
        position: absolute;
        bottom: 0;
        margin-bottom: 7vmin;
        z-index: 1;
        font-size: 16px;
        border-radius: 26px;
    }

    #toggleVisaoMapa {
        margin: 62px 7px 0 0;
    }
}
